@import "./index.css";
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.audience {
    background: #ebd8d1;
    width: 100vw;
    height: 100vh;
    margin: auto;
    position: relative;
}
.main-content {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
}
.interactive {
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
    margin: auto;
}
button.block {
    background: linear-gradient(0deg, #bb95f1, #bb95f1), #81f0f6;
    transition: all 0.25s ease-in-out;
    color: #fff;
    &:hover {
        transition: all 0.25s ease-in-out;
        background: linear-gradient(0deg, #579aea, #579aea), #81f0f6;
    }
    &:active {
        transition: all 0.25s ease-in-out;
        background: linear-gradient(0deg, #6aa2e6, #6aa2e6), #81f0f6;
    }
}

$mainBg: linear-gradient(27.68deg, #ffc500 -37.23%, #f28f99 104.74%), #a4f375;
.question_block {
    background: $mainBg;
    color: #fff;
    text-align: center;
    border-radius: 25px;
}
.audience-container {
    max-width: 1220px;
    margin: auto;
    width: 100%;
    padding-bottom: 63%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    .audience-inner {
        background: url(../img/audience.png) no-repeat center;
        background-size: contain;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        width: 100%;
        height: 100%;
        bottom: 0;
        margin: auto;
    }
}
h2 {
    font-size: 2rem;
}
p {
    margin-bottom: 0.5rem;
}
